import { useState, useEffect } from 'react';
import { useNavigate, useLocation } from 'react-router-dom';
import { useForm, SubmitHandler, Controller } from 'react-hook-form';
import styled from 'styled-components';
import * as Api from '../lib/Api';
import { EclassInput } from 'interfaces/EclassSchedule';

const Container = styled.div`
  flex-direction: column;
`;
const StyledH2 = styled.h2`
  text-align: left;
  padding-left: 20rem;
`;
const StyledH3 = styled.h3`
  text-align: left;
  padding-top: 40px;
  padding-left: 24rem;
`;
const Form = styled.form`
  height: 100%;
  width: 100%;
`;
const EditSubjectTitle = styled.input`
  height: 32px;
  width: 98.6%;
  padding-left: 10px;
  border: 0.5px solid #cdcdcd;
`;
const EditContentWapper = styled.div`
  display: inline-flex;
`;
const EditSubTitleWapper = styled.div`
  width: 7rem;
  margin-left: 200px;
  font-size: 0.9rem;
  color: white;
`;
const EditTypeTitle = styled.div`
  display:flex;
  align-items: center;
  padding-left: 1.6rem;
  height: 35px;
  text-shadow: 1px 1px 1px #506890;
  background-color: #7c95be;
  border: 1px solid #6880a6;
`;
const EditDateTitle = styled.div`
  display:flex;
  align-items: center;
  padding-left: 1.6rem;
  height: 35px;
  text-shadow: 1px 1px 1px #506890;
  background-color: #7c95be;
  border: 1px solid #6880a6;
`;
const EditContentTitle = styled.div`
  display:flex;
  align-items: center;
  padding-left: 1.6rem;
  height: 250px;
  text-shadow: 1px 1px 1px #506890;
  background-color: #7c95be;
  border: 1px solid #6880a6;
`;
const EditContent = styled.div`
  flex-direction: column;
  height: 359px;
  width: 55rem;
  border: 1px solid #cdcdcd;
`;
const EditSubjectType = styled.select`
  height: 35px;
  width: 55rem;
  padding-left: 10px;
  border: 0.5px solid #cdcdcd;
`;
const EditDateWapper = styled.div`
  display: flex;
  height: 36px;
  background-color: #fff;
  border: 0.5px solid #cdcdcd;
`;
const EditStartDate = styled.input`
  height: 36px;
  width: 400px;
  margin-left: 8px;
  padding: 0 10px;
  border: none;
`;
const EditStyledP = styled.p`
  height: 20px;
  margin: 6px 36px;
`;
const EditEndDate = styled.input`
  height: 36px;
  width: 400px;
  padding-left: 10px;
  border: none;
`;
const EditStyledDetail = styled.textarea`
  height: 238px;
  width: 54.25rem;
  padding-left: 10px;
  padding-top: 10px;
  border: 0.5px solid #cdcdcd;
`;
const TitleWapper = styled.div`
  width: 61.9rem;
  height: 39px;
  margin-left: 24.4rem;
  background-color: #e6e6e6;
  border: 1.5px solid #cdcdcd;
`;
const SubjectTitle = styled.div`
  display: flex;
  align-items: center;
  height: 33px;
  width: 50rem;
  padding-left: 10px;
  margin-top: 3px;
  margin-right: 335px;
  font-size: 15px;
  background-color: #e6e6e6;
  border: none;
`;
const ContentWapper = styled.div`
  display: inline-flex;
`;
const SubTitleWapper = styled.div`
  width: 7rem;
  margin-left: 11.8rem;
  font-size: 0.9rem;
  color: white;
`;
const TypeTitle = styled.div`
  display: flex;
  align-items: center;
  padding-left: 1.6rem;
  height: 35px;
  text-shadow: 1px 1px 1px #506890;
  background-color: #7c95be;
  border: 1px solid #6880a6;
`;
const DateTitle = styled.div`
  display:flex;
  align-items: center;
  padding-left: 1.6rem;
  height: 35px;
  text-shadow: 1px 1px 1px #506890;
  background-color: #7c95be;
  border: 1px solid #6880a6;
`;
const ContentTitle = styled.div`
  display:flex;
  align-items: center;
  padding-left: 1.6rem;
  height: 250px;
  text-shadow: 1px 1px 1px #506890;
  background-color: #7c95be;
  border: 1px solid #6880a6;
`;
const Content = styled.div`
  flex-direction: column;
  height: 323px;
  width: 54.9rem;
  font-size: 13px;
  border: 1px solid #cdcdcd;
`;
const SubjectType = styled.div`
  display: flex;
  align-items: center;
  height: 34px;
  width: 54.3rem;
  padding-left: 10px;
  border: 0.5px solid #cdcdcd;
`;
const DateWapper = styled.div`
  display:flex;
  align-items: center;
  height: 35.5px;
  width: 54.3rem;
  padding-left: 10px;
  background-color: #fff;
  border: 1px solid #cdcdcd;
`;
const StyledP = styled.p`
  height: 20px;
  margin: 6px 36px;
`;
const StyledDetail = styled.div`
  text-align: left;
  height: 240px;
  width: 54.3rem;
  padding-left: 10px;
  padding-top: 10px;
  border: 0.5px solid #cdcdcd;
`;
const BtnWapper = styled.div`
  display: flex;
  justify-content: flex-end;
  margin-right: 13rem;
`;
const SubmitButton = styled.button`
  margin: 30px 13px;
  font-size: 13px;
  height: 25px;
  width: 75px;
  color: #67686c;
  background-color: #ebebeb;
  border: 1px solid #9fa4b1;
  border-radius: 5px;
  cursor: pointer;
`;

// memo:     () Component
const EclassDetail = () => {
  // memo:     
  const [isEditing, setIsEditing] = useState(false);
  // memo:   
  const [data, setData] = useState<EclassInput>();
  // memo:    navigate 
  const navigate = useNavigate();
  // memo:     location 
  const location = useLocation();
  // memo: location  subjectName scheduleId
  const subjectName = location.state.subjectName;
  const scheduleId = location.state.scheduleId;

  // memo: react-hook-form    
  const {     
    register,
    handleSubmit,
    reset,
    control,
  } = useForm<EclassInput>({mode : 'onBlur'});

  // memo:     
  useEffect(() => {
    getData();
  }, []);

  // memo:   ,   error message console 
  const getData = async () => {
    try {
      await Api.get(`/schedule/official/${scheduleId}`).then((res) => {
      const result = res.data.result;
      console.log(result);
      setData(result);
    });
    } catch (error) {
      console.error('  :', error);
    }
  };
  console.log(data);

  // memo: react-hook-form     
  const onSubmit: SubmitHandler<EclassInput> = data => putData(data);
  // memo:   ,     ,      ,    reload
  const putData = async ({ title, contents, subjectScheduleType, startDate, endDate}:EclassInput) => {
		try {
      const className = subjectName;
			const putData = { title, contents, subjectScheduleType, startDate, endDate, className };
      console.log(putData);
      startDate <= endDate! ?
			await Api.put(`/schedule/official/${scheduleId}`, putData).then((res) => {
        alert('  .');
        window.location.reload();
			}) : alert('  .');
		} catch (e) {
			alert(e);
		}
	};

  // memo:   ,      
  const delSchedule = async() => {
    await Api.delete(`/schedule/official/${scheduleId}`).then((res) => {
      window.confirm('?');
      navigate(-1);
    });
  }

  // memo:     ,     reset
  const onClickCancleBtn = () => {
    setIsEditing(false)
    reset({...data});
  };

  // memo:  , Controller       
  return (
    <>
      <StyledH2> </StyledH2>
      {isEditing ? (<>
        <Container>
          <StyledH3>  </StyledH3>
          <Form onSubmit={handleSubmit(onSubmit)}>
            {data && (
              <EditContentWapper>
                <EditSubTitleWapper>
                  <EditTypeTitle></EditTypeTitle>
                  <EditTypeTitle> </EditTypeTitle>
                  <EditDateTitle> </EditDateTitle>
                  <EditContentTitle> </EditContentTitle>
                </EditSubTitleWapper>
                <EditContent>
                <Controller
                  control={control}
                  name='title'
                  defaultValue={data.title}
                  rules={{ required: true }}
                  render={({ field }) => (
                  <EditSubjectTitle 
                    id='title' type='text' value={field.value} placeholder=' .' 
                    {...register('title', { required: true })}/>
                  )}/>
                <Controller
                  control={control}
                  name='subjectScheduleType'
                  defaultValue={data.subjectScheduleType}
                  rules={{ required: true }}
                  render={({ field }) => (
                  <EditSubjectType id='subjectScheduleType' value={field.value} {...register('subjectScheduleType', { required: true })}>
                    <option value='ASSIGNMENT'>ASSIGNMENT</option>
                    <option value='TEST'>TEST</option>
                    <option value='PRESENTATION'>PRESENTATION</option>
                  </EditSubjectType>)}/>
                  <EditDateWapper>
                    <Controller
                      control={control}
                      name='startDate'
                      defaultValue={data.startDate}
                      rules={{ required: true }}
                      render={({ field }) => (
                      <EditStartDate id='startDate' type='datetime-local' value={field.value}
                        {...register('startDate', { required: true })}/>)}/>
                    <EditStyledP>~</EditStyledP>
                    <Controller
                      control={control}
                      name='endDate'
                      defaultValue={data.endDate}
                      rules={{ required: true }}
                      render={({ field }) => (
                      <EditEndDate id='endDate' type='datetime-local' value={field.value}
                        {...register('endDate', { required: true })}/>)}/>
                  </EditDateWapper>
                  <Controller
                      control={control}
                      name='contents'
                      defaultValue={data.contents}
                      rules={{ required: true }}
                      render={({ field }) => (
                    <EditStyledDetail id='contents' placeholder=' .' value={field.value}
                      {...register('contents', { required: true })}/>)}/>
                </EditContent>
              </EditContentWapper>)}
              <BtnWapper>
                <SubmitButton type='button' onClick={onClickCancleBtn}></SubmitButton>
                <SubmitButton type='submit'></SubmitButton>
              </BtnWapper>
          </Form>
        </Container></>) : (<>
        {data && (
        <Container>
          <StyledH3>  </StyledH3>
          <Form>
              <TitleWapper>
                <SubjectTitle>{data.title}</SubjectTitle>
              </TitleWapper>
              <ContentWapper>
                <SubTitleWapper>
                  <TypeTitle> </TypeTitle>
                  <DateTitle> </DateTitle>
                  <ContentTitle> </ContentTitle>
                </SubTitleWapper>
                <Content>
                  <SubjectType>{data.subjectScheduleType}</SubjectType>
                  <DateWapper>{data.startDate}<StyledP>~</StyledP>{data.endDate}</DateWapper>
                  <StyledDetail>{data.contents}</StyledDetail>
                </Content>
              </ContentWapper>
              <BtnWapper>
                <SubmitButton type='button' onClick={()=>{setIsEditing(true)}}></SubmitButton>
                <SubmitButton type='button' onClick={delSchedule}></SubmitButton>
              </BtnWapper>
          </Form>
        </Container>)}</>)}
    </>
  )
}

export default EclassDetail;