import EclassMenu from './../Components/EclassMenu';
import EclassClassName from 'Components/EclassClassName';
import { useState, useEffect } from 'react';
import { useNavigate, useLocation } from 'react-router-dom';
import styled from 'styled-components';
import { useRecoilValue } from 'recoil';
import { userInfoState } from 'recoil/Atom'
import * as Api from '../lib/Api';
import { v4 as uuidv4 } from 'uuid';

const ListTitleWapper = styled.div`
  display: flex;
  justify-content: center;
  margin: 40px 0 0 21rem;
  width: 70rem;
  font-size: 0.9rem;
  font-weight: bold;
  color: #fff;
  background-color: #13487c;
`;
const StyledType = styled.div`
  width: 100px;
  margin: 10px 25px;
`;
const StyledTitle = styled.div`
  width: 550px;
  margin: 10px 25px;
`;
const StyledDate = styled.div`
  width: 200px;
  margin: 10px 25px;
`;
const StyledDday = styled.div`
  width: 100px;
  margin: 10px 25px;
`;
const ScheduleList = styled.div`
  display: flex;
  justify-content: center;
  width: 70rem;
  margin: 15px 0;
  padding: 0 0 15px 0;
  font-size: 0.9rem;
  border-bottom: 1px solid #000;
`;
const ScheduleType = styled.span`
  width: 100px;
  margin: 0 25px;
`;
const Title = styled.button`
  width: 550px;
  margin: 0 25px;
  font-size: 0.9rem;
  background-color: #fff;
  border: none;
  cursor: pointer;
  transition: 0.2s;
  &:hover{  
    color: #1971c2;
  }
`;
const EndDate = styled.span`
  width: 200px;
  margin: 0 25px;
`;
const DDay = styled.span`
  width: 100px;
  margin: 0 25px;
`;
const ScheduleWapper = styled.div`
  height: 520px;
  width: 70rem;
  margin-left: 21rem;
`;
const AddButton = styled.button`
  text-align: center;
  color: #fff;
  padding: 9px 18px;
  margin: 15px 0 1.2rem 70rem;
  border: none;
  border-radius: 5px;
  background-color: #228be6;
  font-size: 13px;
  cursor: pointer;
`;
const StyledSpan = styled.span`
  display: block;
  padding-top: 200px;
`;

// memo: userType   
const isStudent = (userType:string|null):boolean => {
  if(userType === "STUDENT") {
    return true
  } return false;
}

// memo:   
const EclassSubject = () =>{
  // memo:   
  const [data, setData] = useState([]);
  // memo:   userType 
  const userType = useRecoilValue(userInfoState).userType;
  // memo:  userType
  const STUDENT = isStudent(userType);
  // memo:    navigate 
  const navigate = useNavigate();
  // memo:     location 
  const location = useLocation();
  // memo: location  subjectId subjectName
  const subjectId = location.state.subjectId;
  const subjectName = location.state.subjectName;

  // memo:     
  useEffect(() => {
    getData();
  }, []);

  // memo:   ,   error message console 
  const getData = async () => {
    try {
      await Api.get(`/schedule/official?subjectName=${subjectName}`).then((res) => {
      const result = res.data.result;
      setData(result);
    });
    } catch (error) {
      console.error('  :', error);
    }
  };
  console.log(data);
  
  // memo:     ,  subjectId, subjectName, scheduleId  
  const moveToDetail = (e: React.MouseEvent<HTMLButtonElement>) => {
    const scheduleId = (e.target as HTMLButtonElement).getAttribute('data-subject-id');
    navigate(`/eclass/detail/${scheduleId}`, { state: { subjectId, subjectName, scheduleId }  })
  }
  // memo:     ,  subjectName  
  const moveToAdd = (e: React.MouseEvent<HTMLButtonElement>) => {
    navigate(`/eclass/add`, {  state: { subjectName } })
  }

  // memo: data   scheduleList 
  const scheduleList = [...data];
  // memo: scheduleList      JSX  
  const subjects = scheduleList
    .map((data) => {
      // memo:       
      const {subjectScheduleType, title, endDate, dday, scheduleId} = data;
      // memo: 'uuidv4()'      
      return(<ScheduleList key={uuidv4()}>
        <ScheduleType>{subjectScheduleType}</ScheduleType>
        <Title onClick={moveToDetail} value={subjectName} data-subject-id={scheduleId}>{title}</Title>
        <EndDate>{endDate}</EndDate>
        <DDay>D{dday}</DDay>
      </ScheduleList>)
    })

  return (
    <>
      <EclassMenu />
      <EclassClassName/>
      <ListTitleWapper>
        <StyledType></StyledType>
        <StyledTitle></StyledTitle>
        <StyledDate> </StyledDate>
        <StyledDday>D-day</StyledDday>
      </ListTitleWapper>
      { data.length > 0 ?
        <ScheduleWapper>
          {subjects}
        </ScheduleWapper> :
        <ScheduleWapper>
          <StyledSpan>  .</StyledSpan>
        </ScheduleWapper>}
      <div>
      { STUDENT ? '' : <AddButton onClick={moveToAdd} value={subjectId} data-subject-name={subjectName}>  </AddButton>}
      </div>
    </>
  )
}

export default EclassSubject;